.header_flex{
    top: 0;
    width: 100%;
    position: fixed;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    z-index: 10;
    padding: 20px;
    transition: all .7s;
    box-shadow: 0 0 0.7142857142857143rem #1011558c;   
    background-color: White;
}

.nav ul{
    display: flex;
    list-style: none;
    
    padding: 0;
    margin-top: 20px;
  
    
}

.nav ul li a{
    text-decoration: none;
   
    color: black;
    padding: 12px;
    margin: auto;
    
   
   
}
.nav ul li a:hover{
 color: blue;
 

}


