.servicios{
background: url(img/filtro/prueba3.jpg)no-repeat center center/cover;
color: white;
position: relative;
z-index: 1;
width: 100%;
}

.servicios::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
   
    left: 0;
/*     background-color: rgba(85, 89, 97, 0.904); */
